import React from "react";

import { HeadFC, HeadProps, PageProps } from "gatsby";
import { graphql } from "gatsby";

import { IProjectsPageContext, IProjectsPageData } from "../types/project.type";

import Banner from "../components/common/banner";
import ContentContainer from "../components/common/contentContainer";
import ProjectPreview from "../components/projects/projectPreview";
import Pagination from "../components/common/pagination";
import { HeadSeo } from "../components/common/metadata";

const Projects = ({
  data,
  pageContext,
}: PageProps<IProjectsPageData, IProjectsPageContext>) => {
  const projects = data.allMarkdownRemark.nodes;
  const { currentPage, pagesQuantity, markupData, lang } = pageContext;

  return (
    <>
      <Banner
        heightClass="h-136"
        title={markupData.bannerTitle}
        text={markupData.bannerText}
        borderBottom
      />
      <ContentContainer className="flex-col items-center p-6 md:p-12">
        <div className="flex flex-wrap w-full justify-center py-14">
          {projects
            ? projects.map((project) => (
                <ProjectPreview
                  key={project.id}
                  project={project}
                  lang={lang}
                  theme="dark"
                  className="basis-full md:basis-3/4 lg:basis-2/5 my-10 md:m-12"
                />
              ))
            : null}
        </div>
        {pagesQuantity > 1 ? (
          <Pagination
            section={"projects"}
            pagesQuantity={pagesQuantity}
            currentPage={currentPage}
            nextPageTitle={pageContext.pagination.next}
          />
        ) : null}
      </ContentContainer>
    </>
  );
};

export default Projects;

export const Head: HeadFC<IProjectsPageData, IProjectsPageContext> = ({
  pageContext: { markupData, site },
  location,
}: HeadProps<IProjectsPageData, IProjectsPageContext>) => (
  <HeadSeo
    title={`${markupData.head} | ${markupData.bannerTitle.toUpperCase()}`}
    siteUrl={site.siteUrl}
    path={location.pathname}
  />
);

export const query = graphql`
  query ($skip: Int!, $limit: Int!, $lang: String) {
    allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "projects" }, language: { eq: $lang } }
      }
      sort: { frontmatter: { date: DESC } }
      limit: $limit
      skip: $skip
    ) {
      nodes {
        id
        frontmatter {
          title
          text
          stack
          cover {
            childImageSharp {
              gatsbyImageData(
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
                width: 600
              )
            }
          }
        }
      }
    }
  }
`;
